<template>
  <div class="cont">
    <v-alert color="#1f7b8a" class="header" dark dense prominent>
      <p class="text-center">فيديوهات الدروس</p>
    </v-alert>
    <div class="row" dir="rtl">
      <div class="col-md-6">
        <div class="form-group">
          <label>الصف الدراسي</label>
          <select v-model="stage" class="form-control">
            <option
              v-for="stage in $store.getters.stages"
              :key="stage.value"
              :value="stage.value"
            >
              {{ stage.text }}
            </option>
          </select>
        </div>
      </div>

      <div class="col-md-6">
        <div class="form-group">
          <label>الدورة</label>
          <select v-model="section" class="form-control">
            <option value="all">الكل</option>
            <option
              v-for="section in stageSections"
              :key="section._id"
              :value="section.number"
            >
              {{ section.name }}
            </option>
          </select>
        </div>
      </div>

      <div class="col-md-6">
        <div class="form-group">
          <label>الدرس</label>
          <select v-model="lesson" class="form-control">
            <option value="all">الكل</option>
            <option v-for="less in lessons" :key="less._id" :value="less.number">
              {{ less.name }}
            </option>
          </select>
        </div>
      </div>
    </div>

    <hr />
    <loading v-if="loading" />
    <div v-if="!loading">
      <hr />
      <div class="videos_section">
        <div v-for="vid in videos" :key="vid._id" class="video_card">
          <div class="video_info">
            <div class="video_desc">
              <h3>{{ vid.section | section(vid.stage) }}</h3>
              <hr />
              <h1>{{ vid.name }}</h1>
              <hr />
              <h3>{{ vid.part }}</h3>
              <p style="color: #999">{{ vid.showDate | moment("dddd, MMMM Do YYYY") }}</p>
              <hr />
              <!-- <h3 class="">فيديو رقم {{ vid.number }}</h3> -->
              <p class="type">{{ vid.stage | stage }}</p>
            </div>
            <hr />
            <div class="video_options">
              <div class="option" @click="() => edit(vid._id)">
                <i class="fa fa-pencil">تعديل</i>
              </div>
              <div class="option" @click="() => deleteVid(vid._id, vid.stage)">
                <i class="fa fa-trash">ازالة</i>
              </div>
              <div
                v-if="lessonCodes"
                class="option"
                @click="$router.push(`/dashboard/lessonCodes/${vid._id}`)"
              >
                <i class="fa fa-bars">الأكواد</i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../../components/loading.vue";
export default {
  components: {
    Loading,
  },
  created() {
    if (this.$route.params.section && this.$route.params.stage) {
      this.section = this.$route.params.section;
      this.stage = this.$route.params.stage;
    }
    this.getVideos();
    axios.get("/getSections").then((res) => {
      this.sections = res.data.sections;
    });
  },
  data() {
    return {
      videos: [],
      id: "",
      video: {},
      ready: false,
      loading: false,
      stage: "6",
      section: "all",
      lesson: "all",
      sections: [],
    };
  },
  methods: {
    getVideos() {
      this.loading = true;
      const filter = { stage: this.stage };
      if (this.section != "all") filter["section"] = this.section;
      if (this.lesson != "all") filter["number"] = this.lesson;
      axios.post("/fetchVideos", filter).then((res) => {
        this.videos = res.data.videos;
        this.loading = false;
      });
    },
    edit(id) {
      this.$router.push({ path: "/dashboard/uploadVideo/" + id });
    },
    deleteVid(id, stage) {
      const confirmed = confirm("هل متاكد من مسح الدرس نهائيا ؟؟؟");
      if (confirmed) {
        axios("/deleteVideo/" + id + "/" + stage).then((res) => {
          this.videos = res.data.videos;
        });
      }
    },
    play(vid) {
      // this.$refs.modal_button.click();
      // this.video = vid;
      // this.ready = true;
    },
  },
  computed: {
    lessonCodes() {
      return this.$store.getters.adminData.lessonCodes;
    },
    stageSections() {
      return this.sections.filter((sec) => sec.stage == this.stage);
    },
    lessons() {
      const filtered = this.$store.getters.lessons.filter((lesson) => {
        return lesson.stage == this.stage && lesson.section == this.section;
      });
      const arr = [];
      filtered.forEach((item) => {
        if (arr.findIndex((it) => it.number == item.number) < 0) {
          arr.push(item);
        }
      });
      // const reduced = filtered.reduce((arr, item) => {
      //   if (arr.findIndex((it) => it.number == item.number) < 0) {
      //     console.log([...arr, item]);
      //     return [...arr, item];
      //   }
      // }, []);

      console.log(arr);
      return arr;
    },
  },
  watch: {
    stage(val) {
      this.getVideos();
    },
    section(val) {
      this.getVideos();
    },
    lesson(val) {
      this.getVideos();
    },
  },
};
</script>

